.interest {
  background-color: var(--grey-color);
  min-height: 150px;
}

.interest .container {
  text-align: center;
}

.interest .container > h2 {
  font-size: calc(20px + (28 - 20) * ((100vw - 300px) / (1600 - 300)));
  color: var(--tertiary-color);
  margin-bottom: 15px;
}

.interest__action {
  display: flex;
  flex-direction: column;
}

.interest__action > button {
  margin-bottom: 10px;
}

@media screen and (min-width: 426px) {
  .interest__action {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 15px;
  }
}

@media screen and (min-width: 769px) {
  .interest .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .interest__action {
    justify-content: flex-end;
  }
}
