footer {
  background-color: var(--primary-color);
  padding: 50px 0;
  color: var(--white-color);
}

footer .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer-left,
.footer-right {
  text-align: center;
}
.footer-left a {
  color: var(--white-color);
}

.footer-right {
  margin-top: 5px;
}

@media screen and (min-width: 769px) {
  footer .container {
    flex-direction: row;
    justify-content: space-between;
  }

  .footer-right {
    text-align: right;
  }

  .footer-left {
    text-align: left;
  }
}
