.section-title {
  width: 100%;
  height: auto;
  text-align: center;
  margin-bottom: 20px;
}

.section-title > p {
  font-size: 14px;
  font-weight: 400;
  color: var(--primary-color);
}

.section-title > h2 {
  font-size: calc(28px + (36 - 28) * ((100vw - 300px) / (1600 - 300)));
  color: var(--tertiary-color);
}
