.jumbotron {
  background-color: var(--grey-color);
  width: 100%;
  min-height: 555px;
  overflow: hidden;
  padding: 30px 0 !important;
}

.jumbotron .container {
  display: flex;
  flex-direction: column-reverse;
  height: 100%;
}

.profile-info {
  align-self: center;
  width: 100%;
}
.profile-info > h1 {
  font-size: calc(32px + (45 - 32) * ((100vw - 300px) / (1600 - 300)));
  color: var(--primary-color);
  line-height: 36px;
}
.profile-info > h3 {
  font-size: calc(28px + (36 - 28) * ((100vw - 300px) / (1600 - 300)));
  font-weight: 300;
  line-height: 36px;
}
.profile-info > p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 20px;
}

.profile-image {
  width: 100%;
  object-fit: cover;
  object-position: center;
  background-color: rgba(0, 109, 182, 0.2);
  max-height: 300px;
  margin-bottom: 20px;
  overflow: hidden;
}

.profile-image > img {
  position: relative;
  top: -45px;
  width: 100%;
  height: auto;
}

@media screen and (min-width: 376px) {
  .profile-image {
    max-height: 340px;
  }

  .profile-info > h1 {
    line-height: 46px;
  }
}

@media screen and (min-width: 556px) {
  .profile-image {
    max-height: 400px;
  }
}

@media screen and (min-width: 769px) {
  .jumbotron .container {
    flex-direction: row;
    align-items: center;
  }

  .profile-image {
    background-color: transparent;
    overflow: visible;
    align-self: flex-end;
  }

  .profile-image > img {
    top: -135px;
  }

  .profile-info {
    padding-top: 50px;
  }
  .profile-info > h1 {
    line-height: 56px;
  }
}
