.spec {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.spec-image {
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.spec-image img {
  width: 100%;
  height: 255px;
  object-fit: cover;
  object-position: center;
}

.spec-items {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.spec-card {
  height: 265px;
  margin: 10px;
}

.card {
  padding: 20px;
  border: 1px solid var(--primary-color);
  text-align: center;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.25);
}

.card-title {
  font-size: calc(20px + (28 - 20) * ((100vw - 300px) / (1600 - 300)));
  color: var(--tertiary-text);
  font-weight: 500;
}

.card-description {
  font-size: 14px;
}

@media screen and (min-width: 568px) {
  .spec-image img {
    height: 300px;
  }

  .spec-items {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }

  .card {
    width: 45%;
  }
}

@media screen and (min-width: 769px) {
  .spec {
    flex-direction: row;
    justify-content: space-between;
  }

  .spec-image {
    width: 40%;
    object-fit: cover;
    object-position: center;
  }

  .spec-image img {
    height: auto;
  }

  .spec-items {
    width: 60%;
    align-items: center;
  }
}
